'use client'

import { useState } from 'react'
import useSWR, { mutate } from 'swr'
import { format } from 'date-fns'
import { Mail, Trash2, CheckCircle, XCircle, Filter, Search, MoreHorizontal } from 'lucide-react'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import {
    Select,
    SelectContent,
    SelectItem,
    SelectTrigger,
    SelectValue,
} from '@/components/ui/select'
import { Badge } from '@/components/ui/badge'
import {
    Table,
    TableBody,
    TableCell,
    TableHead,
    TableHeader,
    TableRow,
} from '@/components/ui/table'
import {
    DropdownMenu,
    DropdownMenuContent,
    DropdownMenuItem,
    DropdownMenuLabel,
    DropdownMenuTrigger,
} from '@/components/ui/dropdown-menu'

const fetcher = async (url: string) => {
    const res = await fetch(url)
    const text = await res.text()
    try {
        return JSON.parse(text)
    } catch (e) {
        console.error(`JSON Parse Error for ${url}:`, text)
        throw e
    }
}

export default function BookingsPage() {
    const [statusFilter, setStatusFilter] = useState('ALL')
    const [searchQuery, setSearchQuery] = useState('')
    const { data: bookings, error, isLoading } = useSWR(
        `/api/bookings?status=${statusFilter}`,
        fetcher
    )

    const handleStatusUpdate = async (id: string, newStatus: string) => {
        try {
            await fetch(`/api/bookings/${id}`, {
                method: 'PUT',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ status: newStatus }),
            })
            mutate(`/api/bookings?status=${statusFilter}`)
        } catch (error) {
            console.error('Failed to update status', error)
        }
    }

    const handleDelete = async (id: string) => {
        if (!confirm('Are you sure you want to delete this booking?')) return

        try {
            await fetch(`/api/bookings/${id}`, {
                method: 'DELETE',
            })
            mutate(`/api/bookings?status=${statusFilter}`)
        } catch (error) {
            console.error('Failed to delete booking', error)
        }
    }

    const getStatusColor = (status: string) => {
        switch (status) {
            case 'CONFIRMED':
                return 'bg-green-500 hover:bg-green-600'
            case 'CANCELLED':
                return 'bg-red-500 hover:bg-red-600'
            default:
                return 'bg-yellow-500 hover:bg-yellow-600'
        }
    }

    // Client-side filtering for search (for now, ideally backend)
    const filteredBookings = bookings?.filter((booking: any) => {
        if (!searchQuery) return true
        const query = searchQuery.toLowerCase()
        return (
            booking.name.toLowerCase().includes(query) ||
            booking.email.toLowerCase().includes(query) ||
            booking.activityTitle.toLowerCase().includes(query)
        )
    })

    if (error) return <div>Failed to load bookings</div>
    if (isLoading) return <div>Loading...</div>

    return (
        <div className="space-y-6">
            <div className="flex items-center justify-between">
                <h1 className="text-3xl font-bold">Bookings</h1>
            </div>

            <div className="flex items-center justify-between gap-4">
                <div className="flex items-center gap-2 flex-1 max-w-sm">
                    <Search className="h-4 w-4 text-muted-foreground" />
                    <Input
                        placeholder="Search bookings..."
                        value={searchQuery}
                        onChange={(e) => setSearchQuery(e.target.value)}
                        className="max-w-sm"
                    />
                </div>
                <div className="flex items-center gap-2">
                    <Filter className="h-4 w-4 text-muted-foreground" />
                    <Select value={statusFilter} onValueChange={setStatusFilter}>
                        <SelectTrigger className="w-[180px]">
                            <SelectValue placeholder="Filter by status" />
                        </SelectTrigger>
                        <SelectContent>
                            <SelectItem value="ALL">All Statuses</SelectItem>
                            <SelectItem value="PENDING">Pending</SelectItem>
                            <SelectItem value="CONFIRMED">Confirmed</SelectItem>
                            <SelectItem value="CANCELLED">Cancelled</SelectItem>
                        </SelectContent>
                    </Select>
                </div>
            </div>

            <div className="rounded-md border">
                <Table>
                    <TableHeader>
                        <TableRow>
                            <TableHead>Customer</TableHead>
                            <TableHead>Service</TableHead>
                            <TableHead>Date</TableHead>
                            <TableHead>Status</TableHead>
                            <TableHead className="text-right">Actions</TableHead>
                        </TableRow>
                    </TableHeader>
                    <TableBody>
                        {filteredBookings?.map((booking: any) => (
                            <TableRow key={booking.id}>
                                <TableCell>
                                    <div className="font-medium">{booking.name}</div>
                                    <div className="text-sm text-muted-foreground">{booking.email}</div>
                                    <div className="text-sm text-muted-foreground">{booking.phone}</div>
                                </TableCell>
                                <TableCell>
                                    <div className="font-medium">{booking.activityTitle}</div>
                                    <div className="text-sm text-muted-foreground">{booking.guests} guests</div>
                                </TableCell>
                                <TableCell>
                                    {format(new Date(booking.date), 'PPP')}
                                </TableCell>
                                <TableCell>
                                    <Badge className={getStatusColor(booking.status)}>
                                        {booking.status}
                                    </Badge>
                                </TableCell>
                                <TableCell className="text-right">
                                    <DropdownMenu>
                                        <DropdownMenuTrigger asChild>
                                            <Button variant="ghost" className="h-8 w-8 p-0">
                                                <span className="sr-only">Open menu</span>
                                                <MoreHorizontal className="h-4 w-4" />
                                            </Button>
                                        </DropdownMenuTrigger>
                                        <DropdownMenuContent align="end">
                                            <DropdownMenuLabel>Actions</DropdownMenuLabel>
                                            <DropdownMenuItem onClick={() => window.location.href = `mailto:${booking.email}`}>
                                                <Mail className="mr-2 h-4 w-4" />
                                                Email Customer
                                            </DropdownMenuItem>
                                            {booking.status !== 'CONFIRMED' && (
                                                <DropdownMenuItem onClick={() => handleStatusUpdate(booking.id, 'CONFIRMED')}>
                                                    <CheckCircle className="mr-2 h-4 w-4 text-green-600" />
                                                    Confirm Booking
                                                </DropdownMenuItem>
                                            )}
                                            {booking.status !== 'CANCELLED' && (
                                                <DropdownMenuItem onClick={() => handleStatusUpdate(booking.id, 'CANCELLED')}>
                                                    <XCircle className="mr-2 h-4 w-4 text-orange-600" />
                                                    Cancel Booking
                                                </DropdownMenuItem>
                                            )}
                                            <DropdownMenuItem onClick={() => handleDelete(booking.id)} className="text-red-600">
                                                <Trash2 className="mr-2 h-4 w-4" />
                                                Delete Booking
                                            </DropdownMenuItem>
                                        </DropdownMenuContent>
                                    </DropdownMenu>
                                </TableCell>
                            </TableRow>
                        ))}
                        {filteredBookings?.length === 0 && (
                            <TableRow>
                                <TableCell colSpan={5} className="h-24 text-center">
                                    No bookings found.
                                </TableCell>
                            </TableRow>
                        )}
                    </TableBody>
                </Table>
            </div>
        </div>
    )
}
